.section {
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background-image: url('../../../assets/backgroundBanner.png');
  background-repeat: no-repeat;
  background-size: cover;

  h1 {
    font-size: 5rem;
    text-align: center;

    span {
      font-weight: bold;
      position: relative;

      &::after {
        content: '';
        height: 4px;
        width: 100%;
        background: linear-gradient(90deg, var(--purple), var(--pink));
        position: absolute;
        left: 0;
        bottom: 0;
      }
    }
  }

  h3 {
    text-align: center;
    width: 70%;
  }

  p {
    text-align: center;
  }

  div {
    display: flex;
    gap: 1rem;
  }
}
.buttn_a {
  text-decoration: none;
}

@media (max-width: 768px) {
  .banner {
    h1 {
      font-size: 2rem;
    }
  }
}
